
import CryptoModal from './CryptoModal.vue';
import axios from 'axios';
import { ref } from 'vue';

export default {
  props: {
    coins: Array,
  },
  data() {
    return {
      selectedCoin: null,
      modalActive: false,
    };
  },
  methods: {
    toggleModal() {
      this.modalActive = !this.modalActive;
    },
    updateModal(newSelectedCoin) {
      this.selectedCoin = newSelectedCoin;
      this.$emit('selectedCoin', this.selectedCoin);
    },
    selectCoin(coin) {
      axios
        .get(`https://api.coingecko.com/api/v3/coins/${coin.id}`)
        .then((response) => {
          console.log(response.data);
          this.selectedCoin = response.data;
        })
        .catch((error) => {
          console.log(error);
        });
      this.$emit('set-selected-coin', coin);
      this.toggleModal();
    },
  },
  components: {
    CryptoModal,
  },
};
